@-webkit-keyframes flicker {
  0%,25%,27%,29%,77%,80%,100% {
    text-shadow: 0.4vw 0.4vh 1rem rgb(55, 248, 255), 0.2vw 0.2vh rgb(255, 55, 121);
  }
