.flex-container {
  padding: 0;
  margin: auto;

  list-style: none;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;

  justify-content: center;
}

.flex-item {
  padding: 0rem;
  width: 200px;
  height: 150px;
  margin-top: 10px;
  color: white;
  font-size: 1em;
  text-align: center;
}

/* OUTRO PRODUCT LIST */
li.flex-item {
  height:auto;
  padding: 0.5rem;
  flex-grow: 1;
  background: #272727bc;
}

