/* DASHBOARD LAYOUT */

div.topPanel {
  padding: 1rem;
  padding-bottom: 0.5rem;
}

div.rowContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding: 1rem;
}

div.flex {
  display: flex;
  flex-grow: 0;
}

div.flexCol {  
  flex-direction: column;
  flex-basis: 1;
}

div.leftPanel {
  background: #00000056;
  flex-grow: 0.3;
  margin-right: 0.5rem;
}

div.rightPanel {
  background: #00000056;
  flex-grow: 1;
}

/* DASBOARD CONTENTS */

h2 {
  padding: 0.5rem;
  margin: 0.5rem;
}

h3 {
  padding: 2rem;
  margin: 0.5rem;
}

/* NOTIFICATIONS */

ul.notifications {
  background: orange;
  color: black;
}

html,
body {
  height: 100%;
  max-width: 100%;
  overflow-x: hidden;
  background: rgb(32, 31, 31);
  line-height: 1;
}

button * {
  outline: none !important;
}

p.content {
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
}

div.content {
  background-color: #27272767;
}

div.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 95%;
  max-width: 1062px;
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding: 0;
  background: #272727bc;
  color: ghostwhite;
  padding-bottom: 0.7rem;
}

