/* SHADOWS */

@-webkit-keyframes shadow {
  0%,
  50%,
  100% {
    box-shadow: 0vw 1vh 3px black;
  }
  25% {
    box-shadow: 0.2vw 0.5vh 10px #27dfa8f1
  }
  76% {
    box-shadow: 0vw 1vh 3px black;
  }
  75%,
  77% {
    box-shadow: 0.1vw 0.5vh 10px #27dfa8f1
  }
}

@keyframes shadow {
  0%,
  50%,
  100% {
    box-shadow: 0vw 1vh 3px black;
  }
  25% {
    box-shadow: 0.2vw 0.5vh 10px #27dfa8f1
  }
  76% {
    box-shadow: 0vw 1vh 3px black;
  }
  75%,
  77% {
    box-shadow: 0.1vw 0.5vh 10px #27dfa8f1
  }
}

